import {sortResources as resources} from './sortResources';
import $N from '@/common/unit';
export default {
	data() {
		return {
			calHeight: 0,
			leftIndex: 0,
			rightIndex: `right${0}`
		}
	},
	computed: {
		// 左侧数据
		leftListRes() {
			let res = resources.map(item => item.text)
			return res
		},
		// 右侧数据
		rightListRes() {
			return resources
		}
	},
	methods: {
		// 左联动右
		leftToright(i){
			this.rightIndex = `right${i}`
		},
		// 右联动左
		rightToleft(e){
			let curScrollTop = e.detail.scrollTop,
				standardVal = $N.Topx(320),
				curIndex = Math.round(curScrollTop / standardVal);
			this.leftIndex = curIndex;
		}
	},
	mounted() {
		// 动态获取高度
		$N.calSurplusHeight({
			pageID: this,
			pos: 'cal',
			isTabBarPage: true,
			success: val => this.calHeight = val 
		})
	}
}
